.category__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.category__link {
  box-sizing: border-box;
  position: relative;
  display: block;
  width: 100%;
  padding: 25px 29px 30px;
  color: #ffffff;
  text-decoration: none;
  outline: none;

  &:hover {
    margin-top: -5px;
    padding-bottom: 35px;
  }

  &:hover .category__icon,
  &:focus .category__icon {
    opacity: 0.7;
  }

  &:active .category__name,
  &:active .category__icon {
    opacity: 0.3;
  }

  &--toys {
    background-color: #56c3b2;

    &:hover,
    &:focus,
    &:active {
      background-color: #46c1ae;
    }
  }

  &--interior {
    background-color: #63d1bb;

    &:hover,
    &:focus,
    &:active {
      background-color: #59d5bc;
    }
  }
}

.category__name {
  width: 150px;
  margin: 0;
  font-size: 19px;
  line-height: 24px;
}

.category__icon {
  position: absolute;
  opacity: 0.5;
  fill: #149080;

  &--interior {
    top: 23px;
    right: 37px;
  }

  &--toys {
    top: 20px;
    right: 27px;
  }
}

@media (min-width: $tablet-width) {
  .category {
    position: relative;
    background-color: #f9f9f9;

    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 24px;
      background-image: url("../img/bg-zigzag-fill.svg");
      background-position: center 12px;
      background-size: 768px 24px;
      background-repeat: repeat-x;
      z-index: 1;
    }
  }

  .category__name {
    font-size: 20px;
    line-height: 30px;
  }

  .category__container {
    padding: 0 50px;
  }

  .category__list {
    display: flex;
  }

  .category__item {
    width: 50%;
  }

  .category__link {
    padding: 29px 35px 30px;
  }

  .category__icon {
    &--interior {
      top: 30px;
      right: 37px;
    }

    &--toys {
      top: 32px;
      right: 34px;
    }
  }
}

@media (min-width: $desktop-width) {
  .category {
    &::before {
      width: 100%;
      height: 32px;
      background-image: url("../img/bg-zigzag-fill.svg");
      background-position: center 14px;
      background-size: auto 37px;
      background-repeat: repeat-x;
      z-index: 1;
    }
  }

  .category__container {
    position: relative;
    width: 1050px;
    margin: 0 auto;
    padding: 0;
  }

  .category__link {
    padding: 28px 36px 31px;
  }
}
